import request from '@/utils/request'; // import localRequest from '@/utils/localRequest'

export function extraffic(data) {
  return request({
    url: '/app/business/external',
    method: 'post',
    data: data
  });
}
export function trafficExternal(data) {
  return request({
    url: '/app/business/enterext',
    method: 'post',
    data: data
  });
}
export function detail(data) {
  return request({
    url: '/app/business/enterInfo',
    method: 'post',
    data: data
  });
}
export function externalDetail(data) {
  return request({
    url: '/app/business/extinfo',
    method: 'post',
    data: data
  });
}